import {Sortable} from '../../../scripts/vendor/draggable';
import SoundFx from '../../SoundFx';
import {debounce} from '../../../scripts/helpers/debounce';

export default function SortableSection() {
  const containers = document.querySelectorAll('.CubesFrame--idSortable');

  const draggable = new Sortable(containers, {
    draggable: '.draggable-source',
    appendTo: '.CubesFrame--idSortable',
    classes: {
      body: 'draggable-container--is-dragging',
    },
  });

  // --- Drag states --- //
  draggable.on('drag:start', evt => {
    evt.originalSource.classList.add('Cube--isCloned');
    SoundFx.Single.play('cubeUp');
  });

  draggable.on(
    'sortable:sorted',
    debounce(() => {
      SoundFx.Single.play('cubeSort');
    }, 60)
  );

  draggable.on('drag:stop', evt => {
    evt.originalSource.classList.remove('Cube--isCloned');
    SoundFx.Single.play('cubeDown');
  });

  return draggable;
}



